'use client';

import { useUser } from "@/src/context/AuthContext";
import { useVehicleIssues } from "@/src/hooks/useVehicleIssues";
import VehicleIssueTable from "@/src/components/vehicleIssues/VehicleIssueTable";
import VehicleIssueUpdateForm from "@/src/components/vehicleIssues/VehicleIssueUpdateForm";
import AssignTechnicianForm from "@/src/components/vehicleIssues/AssignTechnicianForm";
import { useState } from "react";
import { VehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";

export default function VehicleIssuesPage() {
  const { user, companyId, role } = useUser();
  const { issues, updateIssue } = useVehicleIssues(companyId!, user?.uid || "", role || "");
  const [selectedIssue, setSelectedIssue] = useState<VehicleIssue | null>(null);
  const [assigning, setAssigning] = useState<VehicleIssue | null>(null);

  if (!user || !companyId) return <div>Loading...</div>;

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-bold">Vehicle Issues</h1>

      <VehicleIssueTable
        issues={issues}
        onEdit={(issue) => {
          if (role === "company_owner") setAssigning(issue);
          else setSelectedIssue(issue);
        }}
      />

      {selectedIssue && (
        <div className="p-4 border rounded bg-white space-y-4 mt-4">
          <VehicleIssueUpdateForm
            issue={selectedIssue}
            onSave={(data) => {
              updateIssue(selectedIssue.id, data);
              setSelectedIssue(null);
            }}
          />
        </div>
      )}

      {assigning && role === "company_owner" && (
        <div className="p-4 border rounded bg-white space-y-4 mt-4">
          <AssignTechnicianForm
            onAssign={(userId) => {
              updateIssue(assigning.id, { assignedTo: userId, status: "in_progress" });
              setAssigning(null);
            }}
          />
        </div>
      )}
    </div>
  );
}
