// components/vehicleIssues/AssignTechnicianForm.tsx

import { useState } from "react";
import { Button } from "../ui/button";
import { Input } from "../ui/input";

interface Props {
  onAssign: (userId: string) => void;
}

export default function AssignTechnicianForm({ onAssign }: Props) {
  const [techUserId, setTechUserId] = useState("");

  return (
    <div className="space-y-4">
      <Input placeholder="Enter Technician User ID" value={techUserId} onChange={(e) => setTechUserId(e.target.value)} />
      <Button onClick={() => onAssign(techUserId)}>Assign Technician</Button>
    </div>
  );
}
