// hooks/useVehicleIssues.ts

import { useEffect, useState } from "react";
import { collection, query, where, getDocs, updateDoc, doc } from "firebase/firestore";
import { db } from "@/src/firebaseConfig";
import { VehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";

export function useVehicleIssues(companyId: string, userId: string, role: string) {
  const [issues, setIssues] = useState<VehicleIssue[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!companyId) return;
    fetchIssues();
  }, [companyId]);

  async function fetchIssues() {
    setLoading(true);
    let q;

    if (role === "technician") {
      // Technician chỉ xem lỗi chưa ai nhận hoặc đã được assign cho mình
      q = query(collection(db, "vehicleIssues"), where("companyId", "==", companyId));
    } else {
      // Admin → xem tất cả
      q = query(collection(db, "vehicleIssues"), where("companyId", "==", companyId));
    }

    const snap = await getDocs(q);
    const data = snap.docs.map((doc) => ({ id: doc.id, ...doc.data() } as VehicleIssue));
    setIssues(data);
    setLoading(false);
  }

  async function updateIssue(id: string, data: Partial<VehicleIssue>) {
    await updateDoc(doc(db, "vehicleIssues", id), {
      ...data,
      updatedAt: new Date(),
    });
    await fetchIssues();
  }

  return {
    issues,
    loading,
    updateIssue,
    fetchIssues,
  };
}
