import { VehicleIssue } from "@/src/lib/vehicleIssues/vehicleIssueTypes";
import { Button } from "@/src/components/ui/button";

interface Props {
  issues: VehicleIssue[];
  onEdit: (issue: VehicleIssue) => void;
}

export default function VehicleIssueTable({ issues, onEdit }: Props) {
  if (!issues.length) return <div className="p-4 text-center text-gray-400">No issues found.</div>;

  return (
    <table className="w-full border">
      <thead>
        <tr className="bg-gray-100 text-left">
          <th className="p-2">Ebike ID</th>
          <th className="p-2">Issue Type</th>
          <th className="p-2">Status</th>
          <th className="p-2">Reported At</th>
          <th className="p-2">Action</th>
        </tr>
      </thead>
      <tbody>
        {issues.map(issue => (
          <tr key={issue.id}>
            <td className="p-2">{issue.ebikeId}</td>
            <td className="p-2">{issue.issueType}</td>
            <td className="p-2">{issue.status}</td>
            <td className="p-2">{issue.reportedAt.toDate().toLocaleString()}</td>
            <td className="p-2">
              <Button onClick={() => onEdit(issue)}>Update</Button>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  );
}
    